import {
  IAuthRequest,
  CoreAuthorizerManager,
  IAuthorizer,
  IOAuthCredentials,
  IJwtCredentials,
  IAuthrules,
} from 'everiscore';

export class Authorization extends CoreAuthorizerManager {

  rules: IAuthrules;

  type: string;

  authorizer: IAuthorizer;

  authServices: IAuthRequest;

  credentials: IOAuthCredentials | IJwtCredentials;

  constructor(private _rules: IAuthrules,
    private _authorizer: IAuthorizer,
    private _authServices: IAuthRequest) {
    super();
    this.rules = this._rules;
    this.authorizer = this._authorizer;
    this.authServices = this._authServices;
    this.type = this.rules.auth;
    this.credentials = this.rules.credentials || {};
  }
}
