import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpInterceptor,
} from '@angular/common/http';
import { Authorization } from '../auth.services';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor(public auth: Authorization) { }
  intercept(
    req: HttpRequest<any>,
    next: HttpHandler,
  ) {
    const authorizer: any = this.auth.getAuthorizer() || null;
    if (authorizer && this.auth.rules.interceptor) {
      const token = authorizer.getToken();
      const headers = JSON.parse('{"' + this.auth.rules.headerName + '":"' + this.auth.rules.typeToken + ' ' + token + '"}');
      if (token !== '') {
        req = req.clone({
          setHeaders: headers,
        });
      }
    }
    return next.handle(req);
  }
}
